






































import Vue from 'vue';
import Component from 'vue-class-component';
import Alert from './Alert.vue';
import { addWord } from '../service';
import { alertObj } from '../modules';

@Component({
  components: {
    Alert
  }
})
export default class AddWord extends Vue {
  word: string = ''
  meaning: string = ''
  en: string = ''
  cn: string = ''
  isValidate: boolean = true
  errorMsg: string = ''
  showAlert: boolean = false
  alertProp: alertObj = {
    type: 'success',
    message: '添加成功！'
  }
  timerId: number

  displayAlert(): void {
    this.showAlert = true;
    this.showAlert = true;
    this.timerId = window.setTimeout(() => this.showAlert = false, 1200);
  }

  resetFormData() {
    this.word = '';
    this.meaning = '';
    this.en = '';
    this.cn = '';
  }

  onSubmit(): void {
    if (!this.word) {
      this.isValidate = false;
      this.errorMsg = '单词不能为空';
      return;
    }

    addWord({
      word: this.word,
      meaning: this.meaning,
      en: this.en,
      cn: this.cn
    }).then(() => {
      this.alertProp = {
        type: 'success',
        message: '添加成功！'
      };
      this.displayAlert();
      this.resetFormData();
    }).catch(err => {
      console.error(err);
      this.alertProp = {
        type: 'danger',
        message: `添加失败！${err.message}`
      };
      this.displayAlert();
    })
  }

  unmounted() {
    clearTimeout(this.timerId)
  }
}



